#only-Landing .pic {
  width: 200px;
  height: 200px;
  display: inline-block; }

#only-Landing #index-banner {
  height: 800px; }

#only-Landing nav ul a,
#only-Landing nav .brand-logo {
  color: #444; }

#only-Landing p {
  line-height: 2rem; }

#only-Landing .sidenav-trigger {
  color: #26a69a; }

#only-Landing .parallax-container {
  min-height: 380px;
  line-height: 0;
  height: auto;
  padding-top: 10%;
  color: rgba(255, 255, 255, 0.9); }

#only-Landing .parallax-container .section {
  width: 100%; }

@media only screen and (max-width: 690px) {
  #only-Landing .navbar-brand {
    display: none; } }

@media only screen and (max-width: 342px) {
  #only-Landing .intro {
    font-size: 24px; } }

@media only screen and (max-width: 992px) {
  #only-Landing .parallax-container .section {
    position: absolute;
    top: 40%; }
  #only-Landing #index-banner .section {
    top: 10%; } }

@media only screen and (max-width: 600px) {
  #only-Landing #index-banner .section {
    top: 0; } }

#only-Landing .icon-block {
  padding: 0 15px; }

#only-Landing .icon-block .material-icons {
  font-size: inherit; }

#only-Landing footer.page-footer {
  margin: 0; }

#only-Landing body {
  font-family: 'Saira', sans-serif; }

#only-Landing .shadow {
  box-shadow: 0px 1px 8px 1px black; }

